import React, { Component } from "react";
import { CSSTransition } from "react-transition-group";
import style from "./AlertStyles.module.css";

export default class EmptyNameAlert extends Component {
  render() {
    const { alert, field } = this.props;
    console.log(field);
    return (
      <CSSTransition in={alert} classNames={style} timeout={250} unmountOnExit>
        <div className={[style.alert, field==="Name"?style.alert__name : style.alert__message ].join(' ')}>{field} is empty! Enter this field</div>
      </CSSTransition>
    );
  }
}
